import Cookies from 'universal-cookie';
const cookie = new Cookies();
export const linkMain = () => { window.location.href = "/"; };
export const linkSignIn = () => { window.location.href = "/signin"; };
export const linkSignUp = () => { window.location.href = "/signup"; };
export const linkHome = () => { window.location.href = "/home"; };
export const linkSignOut = () => {
    cookie.remove("isAuthenticated");
    linkMain();
  };
export const linkAfterSignIn = () => {
  cookie.set('access_token', "second cookie");
  cookie.set("isAuthenticated", "true");
  linkHome();
};