import React from 'react'
import { Link } from 'react-router-dom';
import Button from '../../components/Button'

interface HeaderProps {
    className: string;
    goodroad: string;
    person: string;
    color: string;
    bell: string;
    kakao: string;
}


const Header: React.FC<HeaderProps> = (props) => {
    // const { className, goodroad, color, bell, kakao} = props; 
    const linkMemberInfo = () => {
        console.info('linkMemberInfo.');
        window.location.href = "/memberinfo";
    };
    const linkMyInfo = () => {
        window.location.href = "/myinfo";
    };
    const linkSignIn = () => {
        window.location.href = "/signin";
    };


    return (
        <div className={props.className}>
            <div className="common-flex-row-wrap-space-between-vcenter" style={{ height: 63 }}>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter" style={{ paddingLeft: 8 }}>
                        <Link to="/"><img src={props.goodroad} /></Link>
                    </div>
                </div>
                <div className="item-lr">
                    <div className="common-flex-row-wrap-vcenter" style={{ paddingRight: 8 }}>
                        <img src={props.person} style={{ marginRight: 0, cursor: "pointer" }}
                            onClick={() => linkMyInfo()} />
                        <span className={props.color} style={{ marginLeft: 10, marginRight: 10 }}>보호자님.</span>
                        <img src={props.bell} style={{ marginRight: 10 }} />
                        <img src={props.kakao} style={{ marginRight: 10 }} />
                        {/* <div className="common-chip--height__small">로그인</div> */}
                        <Button onClick={() => linkSignIn()}>로그인</Button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header